.App {
  margin-left: auto;
  margin-right: auto;
  width: 500px;
}

.header {
  display: flex;
  // margin-left: auto;
  // margin-right: auto;
  // width: 600px;
}

.selected {
  background-color: yellow;
}

.delete {
  display: none;
  background-color: rgb(224, 5, 5);
  height: 40px;
  vertical-align: middle;
  margin: 21px;
  border: none;
  cursor: pointer;
}

.addButton {
  float: right;
  border: none;
  cursor: pointer;
  background-color: lightblue;
}

.removeButton {
  display: none;
  float: right;
  border: none;
  cursor: pointer;
  background-color: tomato;
}

ul {
  li {
    display: flex;
    width: 300px;
    margin-bottom: 3px;
    button {
      margin-left: auto;
    }
  }
}
